.container1 {
  width: 100vw;
  padding: 0 2rem;
  max-width: 120rem;
  margin: auto;
  margin-bottom: 40px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.travel {
  padding: 5rem 0;
  display: block;
}

@media (min-width: 56.25em) {
  html {
    font-size: 70%;
  }
}

.box {
  height: 400px;
  width: 90%;
  margin-right: 20px;
}

.content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
}
.para {
  width: 50%;
  font-size: 20px;
  line-height: 4rem;
}

.heading1 {
  margin-bottom: 30px;
  font-size: 34px;
  font-weight: bolder;
  text-decoration: underline;
  text-decoration-color: red;
}
.perks {
  background-color: gray;
}

.card2 {
  border: 1px solid black;
  border-radius: 5px;
  padding: 2rem 1rem;
  max-width: 20%;
  height: 300px;
  margin-right: 40px;
  background-color: white;
  font-size: 24px;
  font-weight: bolder;
}

.header-perks {
  margin-bottom: 30px;
  font-size: 34px;
  font-weight: bolder;
  text-decoration: underline;
  text-decoration-color: red;
  text-align: left;
  margin-left: 20px;
  margin-top: 20px;
}
.cards {
  margin-bottom: 20px;
  background-color: gray;
  display: flex;
  align-items: left;
}

.link {
  margin-right: 5px;
  font-size: 20px;
  text-decoration: underline;
  text-decoration-color: red;
}

.sky {
  background-color: aquamarine;
  color: white;
}
.people {
  background: red; /* For browsers that do not support gradients */
  background: -webkit-linear-gradient(
    left,
    orange,
    yellow,
    green,
    cyan,
    blue,
    violet
  ); /* For Safari 5.1 to 6.0 */
  background: -o-linear-gradient(
    right,
    orange,
    yellow,
    green,
    cyan,
    blue,
    violet
  ); /* For Opera 11.1 to 12.0 */
  background: -moz-linear-gradient(
    right,
    orange,
    yellow,
    green,
    cyan,
    blue,
    violet
  ); /* For Firefox 3.6 to 15 */
  background: linear-gradient(
    to right,
    orange,
    yellow,
    green,
    cyan,
    blue,
    violet
  );
}

.people-text {
  color: black;
}

.header-join {
  margin-bottom: 30px;
  font-size: 34px;
  font-weight: bolder;
  text-decoration: underline;
  text-decoration-color: red;
  text-align: left;
  margin-left: 20px;
  margin-top: 20px;
}

.img11 {
  height: 100%;
  width: 100%;
  object-fit: cover;
  margin-left: 20px;
}

.contact {
  display: grid;
  grid-template-columns: 2fr 0.8fr;
}

.find-us {
  background-color: red;
}
.find-header {
  margin-top: 20px;
  margin-left: 20px;
  color: white;
  text-align: left;
  font-size: 34px;
}

.contactinfo {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.subhead {
  color: white;
  font-size: 24px;
  margin: 20px 10px;
}

.sub3 {
  color: white;
  font-size: 18px;
  margin: 20px 10px;
}

.cols {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-size: 70%;
  font-family: Roboto, sans-serif;
  margin: 0;
  margin-top: 20px;
  padding: 0;
  box-sizing: inherit;
  margin-bottom: 0;
  background-color: gray;
}

.lin {
  list-style: none;
  margin-top: 10px;
}

.list2 {
  font-size: 70%;
  font-family: Roboto, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

.list1,
.list2 {
  margin-left: 20px;
  margin-top: 20px;
  list-style: none;
}

.col-header {
  font-size: 16px;
  color: white;
  margin-bottom: 10px;
}

li {
  margin-bottom: 10px;
  font-size: 14px;
}

@media (max-width: 700px) {
  .container1 {
    display: flex;
    flex-direction: column;
  }
  .cards {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
  .card2 {
    width: 400px;
  }
  .content {
    display: flex;
    flex-direction: column;
  }
}
