* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
.hero-container {
  background: url("../images/victor-deweerdt-pdz9QzT258k-unsplash.jpg") center
    center/cover no-repeat;
  height: 95vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000 rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero-text {
  color: white;
  font-weight: bold;
  font-size: 36px;
}

.hero-text1 {
  font-size: 50px;
  font-weight: bolder;
}

.btn {
  margin: 10px;
  padding: 10px;
  color: white;
  border: 1px solid white;
  background-color: transparent;
  font-size: 24px;
  font-weight: bolder;
  border-radius: 5px;
}

.btn2 {
  background-color: white;
  color: black;
}

.btn:hover {
  transition: 2s;
  color: black;
  background-color: white;
}
