form {
}

.formcontainer {
  height: 100vh;
  display: flex;
  align-items: left;

  flex-direction: column;
}

.head1 {
  text-align: left;
  font-size: 24px;
  margin-left: 20px;
}

.inp1 {
  font-size: 16px;
  width: 90%;
  padding: 10px;
  margin: 20px 10px;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.inp1::placeholder {
  font-style: normal;
  font-family: "Courier New", Courier, monospace;
}

.btn11 {
  width: 90%;
  padding: 20px 10px;
  text-align: center;
  color: white;
  background-color: red;
  margin-left: 10px;
  margin-bottom: 20px;
  font-size: 24px;
}

@media (max-width: 700px) {
  .container {
    display: flex;
    flex-direction: column;
    position: relative;
  }
}
